.dash-container {
    background: #FAFAFA;
    width: 100%;
    height: 100vh;
}

.bg-container {
    position: fixed;
    bottom: -60px;
    left: 0px;
    width: 250%;
    background: #FAFAFA;
}

@media (min-width: 400px) {
    .bg-container {
        bottom: -100px;
        width: 200%;
    }
}

@media (min-width: 600px) {
    .bg-container {
        bottom: -100px;
        width: 150%;
    }
}

@media (min-width: 800px) {
    .bg-container {
        bottom: -110px;
        width: 150%;
    }
}

@media (min-width: 1000px) {
    .bg-container {
        width: 100%;
        bottom: -130px;
    }
}

@media (min-width: 1100px) {
    .bg-container {
        bottom: -120px;
    }
}

@media (min-width: 1300px) {
    .bg-container {
        bottom: -160px;
    }
}

.bg-container img {
    opacity: 0.3;
    width: 100%;
}

.logo-container {
    position: fixed;
    z-index: 1000;
    max-width: 300px;
    width: 80%;
    top: 80px;
    left: 50%;
    transform: translate(-50%, -50%);
}

.logo-container img {
    width: 100%;
    opacity: 0.5;
}

.popup-container {
    position: fixed;
    z-index: 10000;
    margin: auto;
    width: 100%;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.create-acc-container h2,
.login-container h2 {
    text-align: center;
    padding: 30px 0px 0px;
    font-size: 20px;
    color: #3E7927;
}

@media (min-width: 500px) {
    .create-acc-container h2,
    .login-container h2 {
        text-align: center;
        padding: 30px 0px 0px;
        font-size: 26px;
        color: #3E7927;
    }
}

.create-acc-inputs {
    height: 360px;
    overflow: scroll;
    overflow-x: hidden;
    max-width: 780px;
    width: 95%;
    margin: auto;
}

.create-acc-row {
    max-width: 490px;
    width: 100%;
    margin: auto;
}

.login-row {
    max-width: 425px;
    width: 90%;
    margin: auto;
}

.last-row {
    margin-bottom: 50px;
}

label {
    position: relative;
    left: 0px;
    color: #3E7927;
    font-size: 10px;
}

.label-input, .label-select {
    margin: 15px auto;
    width: 95%;
}

@media (min-width: 500px) {
    .create-acc-row .label-input, 
    .create-acc-row .label-select {
        margin: 10px;
        max-width: 300px;
        width: 45%;
    }
}

/* .label-select label {
    position: relative;
    left: 4px;
} */

.label-select select {
    padding: 5px 0px 2px;
}

.create-acc-row *,
.login-row * {
    display: block;
}

@media (min-width: 500px) {
    .create-acc-row * {
        display: inline-block;
    }
}

.create-acc-row input,
.login-row input {
    display: block;
    font-size: 16px;
    border: none;
    border-bottom: 1px solid grey;
    padding: 5px 0px 2px;
    color: #222;
    margin: 0px auto;
}

.login-row input {
    width: 100%;
    margin: auto;
}

.create-acc-row input {
    width: 100%;
}

.create-acc-row select {
    color: #222;
    display: block;
    font-size: 18px;
    border: none;
    border-bottom: 1px solid grey;
}

#maritalStatus {
    width: 220px;
}

.create-acc-row h4 {
    display: inline-block;
}

.dep-container h4 {
    color: #888;
    font-weight: 400;
    margin: 10px;
}

.dep-container input {
    width: 100%;
    max-width: 40px;
    margin: 0px;
}

.dep-container .label-input {
    display: inline-block;
    max-width: 40px;
}

.date-container h4, .date-container input {
    display: inline-block;
    margin: 10px;
    color: #888;
    font-weight: 400;
    font-family: "Yu Gothic UI";
}

.date-container input {
    color: #222;
    width: 170px;
}

.create-acc-container {
    position: relative;
    width: 95%;
    max-width: 800px;
    height: 520px;
    margin: 180px auto 0px;
    background: white;
    z-index: 9999;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}

.login-container {
    position: relative;
    width: 95%;
    max-width: 600px;
    height: 400px;
    margin: 220px auto 0px;
    background: white;
    z-index: 9999;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}

@media (min-width: 500px) {
    .login-container {
        height: 420px;
    }
}

.acc-btn {
    width: 100%;
    margin: 25px auto;
    background: none;
    border: none;
    color: #666;
    opacity: 0.8;
}

.acc-btn:hover {
    opacity: 1;
    cursor: pointer;
}

.acc-btn p {
    margin: 3px;
    display: inline-block;
    text-align: center;
}

.submit-container {
    width: 88%;
    max-width: 470px;
    margin: 30px auto;
}

.submit-btn {
    font-size: 16px;
    background: #3E7927;
    border: none;
    border-radius: 6px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    color: white;
    padding: 8px 0px;
    width: 100%;
    margin: auto;
    text-align: center;
}

@media (min-width: 500px) {
    .submit-btn {
        font-size: 20px;
    }
}

.submit-btn:hover {
    background: #396e23;
    cursor: pointer;
}

.submit-inactive {
    background: none;
    color: #ccc;
    box-shadow: 0px 0px 0px 1px #ccc;
}

.submit-inactive:hover {
    background: none;
    cursor: default;
    pointer-events: none;
}

.login-inputs .submit-container {
    max-width: 400px;
    margin: 20px auto 0px;
}

.login-inputs .acc-btn {
    margin: 20px auto 30px;
}

.login-success {
    position: fixed;
    z-index: 9999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}


/* ========== Scrollbar ========== */

/* width */
::-webkit-scrollbar {
    width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 50px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #8fc77c;
    border-radius: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #73a761;
}

@media (min-width: 600px) {
    /* width */
    ::-webkit-scrollbar {
        width: 8px;
    }
    
    /* Track */
    ::-webkit-scrollbar-track {
        background: #f1f1f1;
        border-radius: 50px;
    }
    
    /* Handle */
    ::-webkit-scrollbar-thumb {
        background: #8fc77c;
        border-radius: 50px;
    }
    
    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
        background: #73a761;
    }
}
